@font-face {
  font-family: "Open sans";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/opensanslight.woff") format("woff"), url("../fonts/opensanslight.woff2") format("woff2");
}
@font-face {
  font-family: "Open sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff") format("woff"), url("../fonts/opensans.woff2") format("woff2");
}
@font-face {
  font-family: "Open sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff") format("woff"), url("../fonts/opensansbold.woff2") format("woff2");
}
body {
  color: #283645;
  font-family: 'Open sans', Arial, Helvetica, sans-serif;
  min-width: 320px;
  position: relative;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  width: 100%;
  padding-left: 6.25%;
  padding-right: 6.25%;
}
@media (min-width: 660px) {
  .container {
    padding-left: 3.03%;
    padding-right: 3.03%;
  }
}
@media (min-width: 960px) {
  .container {
    max-width: 960px;
    padding-left: 10px;
    padding-right: 10px;
    margin: auto;
  }
}
.page-header {
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
}
.page-header--menu-opened {
  background-color: #283645;
  padding-bottom: 0px;
}
.page-header--no-js {
  background-color: #283645;
  padding-bottom: 0px;
}
.page-header__logo {
  height: 66px;
  display: inline-flex;
  align-items: center;
}
.main-nav {
  margin-left: -6.25%;
  margin-right: -6.25%;
  min-height: 255px;
  position: absolute;
  width: 100%;
}
.main-nav__toggle {
  position: absolute;
  top: -45px;
  right: 6.25%;
  z-index: 2;
  display: block;
  width: 50px;
  height: 24px;
  border: none;
  background-color: transparent;
}
.page-header--menu-closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}
.page-header--menu-closed .main-nav__toggle:active::before {
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 8px 0 0 rgba(255, 255, 255, 0.3), 0 16px 0 0 rgba(255, 255, 255, 0.3);
}
.page-header--menu-opened .main-nav__toggle {
  top: -45px;
  right: 6.25%;
  width: 24px;
  height: 23px;
}
.page-header--menu-opened .main-nav__toggle::before,
.page-header--menu-opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 10px;
  left: 0px;
  width: 24px;
  height: 2px;
  background-color: #ffffff;
}
.page-header--menu-opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.page-header--menu-opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.page-header--menu-opened .main-nav__toggle:active::before,
.page-header--menu-opened .main-nav__toggle:active::after {
  background-color: rgba(255, 255, 255, 0.3);
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__link {
  display: block;
  text-align: center;
  padding-top: 19px;
  padding-bottom: 23px;
  color: #ffffff;
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 32px;
  background-color: #283645;
  box-shadow: 0 1px 0 #000000, inset 0 1px 0 rgba(255, 255, 255, 0.15);
}
.main-nav__link[href]:hover {
  color: #d22856;
}
.main-nav__link[href]:active {
  color: rgba(210, 40, 86, 0.3);
}
@media (max-width: 959px) {
  .page-header--menu-closed .main-nav__list {
    display: none;
  }
  .page-header--no-js .main-nav {
    position: relative;
    margin: 0;
    left: -6.25%;
    right: -6.25%;
    width: 112.5%;
  }
  .page-header--no-js .main-nav__list {
    display: block;
  }
}
@media (min-width: 660px) {
  .main-nav {
    margin-left: -3.03%;
    margin-right: -3.03%;
  }
  .main-nav__toggle {
    right: 3.03%;
  }
}
